.home-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.button-container {
  margin-top: 20px;
  display: flex;
  flex-direction: column; 
  align-items: center; 
}

.button {
  margin-bottom: 10px;
  padding: 0;
  width: 200px;
  height: 125px;
  background-image: url('../public/pic.jpg'); 
  background-size: 100% 100%;
  color: #fff;
  border: none;
  cursor: pointer;
}

